<!--  -->
<template>
  <div class="">
    message.vue
    <ul >
        <li v-for="m in  messlist" :key="m.id">
            <!-- 跳转路由并携带query参数,to的字符串写法 -->
            <router-link :to="`/home/message/detail?id=${m.id}&title=${m.title}`">{{ m.title }}</router-link>
            <router-link :to="`/home/message/detail/${m.id}/${m.title}`">{{ m.title }}</router-link>
            <!-- 跳转路由并携带query参数,to的对象写法 -->
            <router-link :to="{
                  name:'xiangqing',
                  query:{
                        id:m.id,
                      title:m.title
                    }
                }">{{ m.title }}</router-link>
                <button @click="pushshow(m)">push 查看</button>
                <button @click="replaceshow(m)">replace 查看</button>
        </li>
    </ul>
    <hr>
    <router-view></router-view>
  </div>
</template>
<script>
export default {
    name:"message",
    data() {
        return {
            messlist:[
                {id:'001',title:'消息001'},
                {id:'002',title:'消息002'},
                {id:'003',title:'消息003'},
                {id:'004',title:'消息004'},

            ]
        }
    },
    components:{

    },
    methods:{
        pushshow(m){
            this.$router.push({
                name:'xiangqing',
                query:{
                    id:m.id,
                    title:m.title
                }
            })
        },
        replaceshow(m){
            this.$router.replace({
                name:'xiangqing',
                query:{
                    id:m.id,
                    title:m.title
                }
            })
        }
    },
    created() {

    },
    mounted() {

    }
}
</script>
